import React from 'react';
import { Col, Row } from 'reactstrap';

const NoHeader = () => (
  <div>
    <Row className="justify-content-md-center">
      <Col sm="10" xs="12">
        <h1><em>No Header</em></h1>
      </Col>
    </Row>
  </div>
);

export default NoHeader;



